"use client";

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import Card from '../../../components/Card';
import Button from '../../../components/Button';
import Spinner from '../../../components/Spinner';
import { useToast } from '../../../components/ToastContext';

interface Appointment {
  _id: string;
  status: string;
  payment: any;
  doctor_id: any;
  clinic_id: any;
  room_id: any;
  createdAt: string;
}

export default function PatientDashboard() {
  const router = useRouter();
  const [user, setUser] = useState<any>(null);
  const [appointments, setAppointments] = useState<Appointment[]>([]);
  const [loading, setLoading] = useState(true);
  const [activeTab, setActiveTab] = useState('appointments');
  const { addToast } = useToast();

  useEffect(() => {
    const token = localStorage.getItem('token');
    const userRole = localStorage.getItem('userRole');
    const userStr = localStorage.getItem('user');

    if (!token || userRole !== 'patient') {
      router.push('/login');
      return;
    }

    if (userStr) {
      const parsedUser = JSON.parse(userStr);
      setUser(parsedUser);
      fetchAppointments(parsedUser.id);
    }
  }, []);

  const fetchAppointments = async (patientId: string) => {
    try {
      setLoading(true);
      const response = await fetch(`/api/appointments/book?patient_id=${patientId}`);
      const data = await response.json();
      setAppointments(data);
    } catch (error) {
      console.error('Error fetching appointments:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleLogout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    localStorage.removeItem('userRole');
    router.push('/');
  };

  const handleCancelAppointment = async (appointmentId: string) => {
    try {
      const res = await fetch(`/api/appointments/${appointmentId}`, {
        method: 'DELETE',
      });
      if (!res.ok) throw new Error('Cancel failed');
      setAppointments((prev) => prev.filter((a) => a._id !== appointmentId));
      addToast({ type: 'success', message: 'Appointment cancelled' });
    } catch (error: any) {
      console.error('Error cancelling appointment:', error);
      addToast({ type: 'error', message: error?.message || 'Cancel failed' });
    }
  };

  if (!user) {
    return (
      <div className="min-h-screen bg-gradient-to-b from-slate-50 via-white to-slate-50 flex items-center justify-center">
        <div className="flex flex-col items-center gap-4">
          <Spinner className="h-12 w-12 text-indigo-600" />
          <p className="text-slate-600 font-medium">Loading dashboard...</p>
        </div>
      </div>
    );
  }

  const upcomingAppointments = appointments.filter((a) => a.status !== 'completed' && a.status !== 'cancelled');
  const pastAppointments = appointments.filter((a) => a.status === 'completed' || a.status === 'cancelled');

  const getStatusBadgeColor = (status: string) => {
    switch (status) {
      case 'pending': return 'bg-amber-50 text-amber-700 border-amber-200';
      case 'confirmed': return 'bg-green-50 text-green-700 border-green-200';
      case 'completed': return 'bg-slate-50 text-slate-700 border-slate-200';
      case 'cancelled': return 'bg-red-50 text-red-700 border-red-200';
      default: return 'bg-indigo-50 text-indigo-700 border-indigo-200';
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-b from-slate-50 via-white to-slate-50">
      <div className="max-w-7xl mx-auto px-4 lg:px-8 py-16 lg:py-24">
        {/* Header */}
        <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4 mb-12">
          <div className="space-y-2">
            <h1 className="text-4xl lg:text-5xl font-bold text-slate-900">Dashboard</h1>
            <p className="text-slate-600 text-lg">Manage your appointments and profile</p>
          </div>
          <Button variant="destructive" onClick={handleLogout} className="w-full sm:w-auto">
            <span className="mr-2">üö™</span> Logout
          </Button>
        </div>

        {/* Profile Card */}
        <Card className="mb-12 p-8 border border-slate-100 bg-white">
          <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-6">
            <div className="flex items-center gap-6">
              <div className="w-16 h-16 rounded-full bg-gradient-to-br from-indigo-100 to-blue-100 flex items-center justify-center text-3xl border-2 border-indigo-200">
                üë§
              </div>
              <div className="space-y-1">
                <h2 className="text-2xl font-bold text-slate-900">Welcome back, {user.full_name}!</h2>
                <div className="flex flex-col sm:flex-row sm:items-center gap-2 text-slate-600">
                  <span className="flex items-center gap-1">üìß {user.email}</span>
                  <span className="hidden sm:inline text-slate-400">‚Ä¢</span>
                  <span className="flex items-center gap-1">üìû {user.phone}</span>
                </div>
              </div>
            </div>
            <Link href="/doctors" className="w-full sm:w-auto">
              <Button className="w-full sm:w-auto">
                <span className="mr-2">‚ûï</span> Book Appointment
              </Button>
            </Link>
          </div>
        </Card>

        {/* Stats */}
        <div className="grid grid-cols-1 sm:grid-cols-3 gap-6 mb-12">
          <Card className="p-6 border border-slate-100 text-center space-y-2">
            <div className="text-3xl">üìÖ</div>
            <p className="text-slate-600 font-medium text-sm">Upcoming</p>
            <p className="text-4xl font-bold text-indigo-600">{upcomingAppointments.length}</p>
          </Card>
          <Card className="p-6 border border-slate-100 text-center space-y-2">
            <div className="text-3xl">‚úì</div>
            <p className="text-slate-600 font-medium text-sm">Completed</p>
            <p className="text-4xl font-bold text-green-600">{pastAppointments.filter(a => a.status === 'completed').length}</p>
          </Card>
          <Card className="p-6 border border-slate-100 text-center space-y-2">
            <div className="text-3xl">üìä</div>
            <p className="text-slate-600 font-medium text-sm">Total</p>
            <p className="text-4xl font-bold text-slate-900">{appointments.length}</p>
          </Card>
        </div>

        {/* Tabs */}
        <div className="flex gap-2 mb-8 border-b border-slate-200 overflow-x-auto">
          {[
            { id: 'appointments', label: 'Upcoming', icon: 'üìÖ', count: upcomingAppointments.length },
            { id: 'history', label: 'History', icon: 'üìã', count: pastAppointments.length },
            { id: 'profile', label: 'Profile', icon: '‚öôÔ∏è', count: null }
          ].map((tab) => (
            <button
              key={tab.id}
              onClick={() => setActiveTab(tab.id)}
              className={`px-6 py-3 font-semibold whitespace-nowrap transition-all border-b-2 flex items-center gap-2 ${
                activeTab === tab.id
                  ? 'border-indigo-600 text-indigo-600'
                  : 'border-transparent text-slate-600 hover:text-slate-900'
              }`}
            >
              <span>{tab.icon}</span>
              {tab.label}
              {tab.count !== null && <span className="ml-1 bg-indigo-100 text-indigo-700 rounded-full px-2 py-0.5 text-xs font-bold">{tab.count}</span>}
            </button>
          ))}
        </div>

        {/* Content */}
        {activeTab === 'appointments' && (
          <div>
            {loading ? (
              <div className="flex flex-col items-center justify-center py-24 space-y-4">
                <Spinner className="h-12 w-12 text-indigo-600" />
                <p className="text-slate-600 font-medium">Loading appointments...</p>
              </div>
            ) : upcomingAppointments.length === 0 ? (
              <div className="bg-white rounded-2xl border-2 border-dashed border-slate-300 p-16 text-center space-y-4">
                <p className="text-5xl">üìÖ</p>
                <p className="text-slate-600 text-lg font-medium">No upcoming appointments</p>
                <p className="text-slate-500 mb-6">Book an appointment with a doctor to get started</p>
                <Link href="/doctors">
                  <Button>Browse Doctors</Button>
                </Link>
              </div>
            ) : (
              <div className="space-y-4">
                {upcomingAppointments.map((appointment) => (
                  <Card key={appointment._id} className="p-6 border border-slate-100 hover:border-indigo-300 hover:shadow-lg transition-all">
                    <div className="flex flex-col sm:flex-row sm:items-start sm:justify-between gap-6">
                      {/* Left: Doctor & Clinic Info */}
                      <div className="flex gap-4 flex-1">
                        <div className="w-12 h-12 rounded-lg bg-linear-to-br from-indigo-100 to-blue-100 flex items-center justify-center text-2xl shrink-0">
                          üë®‚Äç‚öïÔ∏è
                        </div>
                        <div className="flex-1 space-y-2">
                          <h3 className="text-lg font-bold text-slate-900">Dr. {appointment.doctor_id?.full_name}</h3>
                          <p className="text-sm text-slate-600">{appointment.doctor_id?.specializations?.join(', ')}</p>
                          <div className="flex items-center gap-2 text-sm text-slate-600 mt-3">
                            <span>üè•</span>
                            <span>{appointment.clinic_id?.name}</span>
                            <span>‚Ä¢</span>
                            <span>Room {appointment.room_id?.room_number}</span>
                          </div>
                        </div>
                      </div>

                      {/* Right: Date, Status & Actions */}
                      <div className="flex flex-col sm:items-end gap-3 sm:gap-4">
                        <div className="space-y-1">
                          <p className="text-sm text-slate-500 font-medium uppercase tracking-wide">Date</p>
                          <p className="text-lg font-bold text-slate-900">
                            {new Date(appointment.createdAt).toLocaleDateString('en-US', { weekday: 'short', month: 'short', day: 'numeric' })}
                          </p>
                        </div>
                        <span className={`px-4 py-2 rounded-full text-sm font-semibold border capitalize ${getStatusBadgeColor(appointment.status)}`}>
                          {appointment.status}
                        </span>
                        <div className="flex gap-2">
                          <Button variant="outline" size="sm">
                            <span className="mr-1">‚ÑπÔ∏è</span> Details
                          </Button>
                          <Button
                            variant="destructive"
                            size="sm"
                            onClick={() => handleCancelAppointment(appointment._id)}
                          >
                            <span className="mr-1">‚úï</span> Cancel
                          </Button>
                        </div>
                      </div>
                    </div>
                  </Card>
                ))}
              </div>
            )}
          </div>
        )}

        {activeTab === 'history' && (
          <div>
            {pastAppointments.length === 0 ? (
              <div className="bg-white rounded-2xl border-2 border-dashed border-slate-300 p-16 text-center space-y-4">
                <p className="text-5xl">üìã</p>
                <p className="text-slate-600 text-lg font-medium">No appointment history</p>
                <p className="text-slate-500">Your completed appointments will appear here</p>
              </div>
            ) : (
              <div className="space-y-4">
                {pastAppointments.map((appointment) => (
                  <Card key={appointment._id} className="p-6 border border-slate-100 opacity-85 hover:opacity-100 transition-opacity">
                    <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
                      <div className="space-y-1">
                        <h3 className="font-bold text-slate-900">Dr. {appointment.doctor_id?.full_name}</h3>
                        <p className="text-sm text-slate-600">{appointment.clinic_id?.name}</p>
                      </div>
                      <div className="flex items-center gap-4">
                        <div className="text-right">
                          <p className="text-sm text-slate-500">Date</p>
                          <p className="font-semibold text-slate-900">
                            {new Date(appointment.createdAt).toLocaleDateString()}
                          </p>
                        </div>
                        <span className={`px-3 py-1.5 rounded-full text-sm font-semibold border capitalize ${getStatusBadgeColor(appointment.status)}`}>
                          {appointment.status}
                        </span>
                      </div>
                    </div>
                  </Card>
                ))}
              </div>
            )}
          </div>
        )}

        {activeTab === 'profile' && (
          <Card className="p-8 border border-slate-100 max-w-2xl">
            <div className="space-y-6">
              <div>
                <h2 className="text-2xl font-bold text-slate-900 mb-6">Edit Profile</h2>
                <div className="space-y-4">
                  <div className="space-y-2">
                    <label className="block text-sm font-semibold text-slate-700">Full Name</label>
                    <input
                      type="text"
                      value={user.full_name}
                      disabled
                      className="w-full px-4 py-2.5 rounded-lg border border-slate-200 bg-slate-50 text-slate-600 font-medium"
                    />
                  </div>
                  <div className="space-y-2">
                    <label className="block text-sm font-semibold text-slate-700">Email</label>
                    <input
                      type="email"
                      value={user.email}
                      disabled
                      className="w-full px-4 py-2.5 rounded-lg border border-slate-200 bg-slate-50 text-slate-600 font-medium"
                    />
                  </div>
                  <div className="space-y-2">
                    <label className="block text-sm font-semibold text-slate-700">Phone</label>
                    <input
                      type="tel"
                      value={user.phone}
                      disabled
                      className="w-full px-4 py-2.5 rounded-lg border border-slate-200 bg-slate-50 text-slate-600 font-medium"
                    />
                  </div>
                  <div className="pt-4 border-t border-slate-100 bg-indigo-50 rounded-lg p-4 space-y-2">
                    <p className="text-sm font-semibold text-indigo-900">üîí Account Security</p>
                    <p className="text-sm text-indigo-800">Profile editing features coming soon</p>
                  </div>
                </div>
              </div>
            </div>
          </Card>
        )}
      </div>
    </div>
  );
}
